.btn {
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.btn.hover_all:before, .btn.hover_all:after {
  bottom: 6px;
  transition: width 0.2s 0.35s ease-out;
  width: 30%;
  height: 1px;
  background-color: #9A9A9A;
  z-index: 2;
}

.btn.hover_all:before {
  right: 43%;
}

.btn.hover_all:after {
  left: 43%;
}

.btn.hover_all:hover:before, .btn.hover_all:hover:after {
  width: 50%;
  transition: width 0.2s ease-in;
}

.btn span {
  color: #9A9A9A;
  position: relative;
  display: inline-block;
  font-weight: bold;
  font-size: 0.8125rem;
  top: 0;
  left: 0;
  width: 100%;
  padding: 15px 20px;
  transition: 0.3s;
}

.btn span:before, .btn span:after {
  width: 0%;
  height: 0%;
  background: transparent;
  opacity: 0;
  z-index: 2;
  transition: width 0.2s ease-in, height 0.15s 0.2s linear, opacity 0s 0.35s;
}

.btn span:before {
  bottom: 0;
  left: 0;
  border-left: 1px solid #9A9A9A;
  border-top: 1px solid #9A9A9A;
}

.btn span:after {
  bottom: 0;
  right: 0;
  border-right: 1px solid #9A9A9A;
  border-top: 1px solid #9A9A9A;
}

.btn span:hover {
  color: #9A9A9A;
}

.btn span:hover:before, .btn span:hover:after {
  width: 51%;
  height: 96%;
  opacity: 1;
  transition: height 0.2s 0.2s ease-in, width 0.2s 0.4s linear, opacity 0s 0.2s;
}

